.item{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;

    background-color: snow;
    height: 2rem;
    width: 100%;

    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    
}

.item:hover{
    background-color: crimson;
}

.item-name{
    /*Hide if its too big text*/
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    text-align: left;

    min-width: 10rem;
    max-width: 20rem;
    /* padding-left: 2rem; */
    padding-right: 2rem;
}

.item-size{
    padding-right: 2rem;
}
